<template>
  <div class="user-field row p-2 m-4 ms-2 me-2">
    <div class="col-6">
      <div class="infos">
        <span class="fw-bold">
          {{ virus.virusName }}
          <span style="color: grey;font-size: 12px;">
              {{ virus.added }}
          </span>
        </span>
      </div>
    </div>
    <div class="actions m-0 col-6 d-flex justify-content-end">
      <div class="user-infos d-flex justify-content-center ps-3">
        <button
          class="infos-button"
          @click="deleteVirus"
          >supprimer</button
        >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "virusField",
  props: {
    virus: Object,
  },
  data() {
    return {};
  },
  methods: {
    deleteVirus() {
      this.loader = this.$loading.show({
        blur: "10px",
        loader: "dots",
        color: "#5a58c3",
        opacity: 1,
      });
      this.$store
        .dispatch("DELETE_VIRUS", {
          uid: this.virus.uid,
        })
        .then((data) => {
          this.loader.hide();
          this.$swal.fire({
            position: "top-end",
            icon: "success",
            title: data.msg,
            showConfirmButton: false,
            timer: 2500,
            iconColor: "#5a58c3",
            timerProgressBar: true,
          });
          this.redirectToUsers();
        })
        .catch((data) => {
          this.loader.hide();
          this.$swal.fire({
            position: "top-end",
            icon: "error",
            title: data.response.data.msg,
            showConfirmButton: false,
            timer: 2500,
            iconColor: "#5a58c3",
            timerProgressBar: true,
          });
          if (data.response.status === 401) {
          this.$store.dispatch("LOGOUT");
        }
        });
    },
  }
};
</script>
<style scoped>
.user-name {
  font-size: 15px;
}
.user-field {
  border-radius: 11px;
  background-color: white;
  box-shadow: 0px 0px 13px 3px rgba(140, 140, 140, 0.12);
}
.infected-card {
  border-radius: 10px;
  padding: 5px;
  font-size: 10px;
  font-family: main-bold;
  color: white;
}
.user-infos {
  align-items: center;
}
.date-joined {
  font-size: 12px;
}
.actions-icons i {
  cursor: pointer;
}
.infos-button {
  background-color: red !important;
  color: white !important;
  padding: 8px;
  border: none;
  border-radius: 20px;
  font-size: 11px;
  font-weight: bold;
}
</style>
<style scoped>
a {
  text-decoration: none;
}
</style>
